<template>
    <layout :showBack="isEdit" :title="isEdit ? '编辑材料信息' : '新增材料信息'">
        <div class="edit-container">
            <span class="title">基本信息</span>
            <van-form @submit="onSubmit">
                <van-field v-model="name"
                    name="材料名字"
                    label="材料名字"
                    placeholder="请输入材料名字"
                    :rules="nameRule"/>
                <!-- clearable在添加了readonly的时候会失效 -->
                <van-field v-model="parentName"
                    name="所属大类"
                    label="所属大类"
                    clearable
                    readonly
                    placeholder="请选择材料所属大类">
                    <template #button>
                        <van-button size="small" @click="pickType" native-type="button" type="primary">选择大类</van-button>
                    </template>
                </van-field>
                <van-field v-model="remark"
                    name="备注"
                    label="备注"
                    placeholder="请输入备注"/>
                <div class="btn">
                    <van-button round block type="primary" native-type="submit">提交</van-button>
                </div>
            </van-form>
        </div>
        <van-popup v-model="show" position="bottom" :style="{ height: '308px' }">
            <van-picker title="选择材料所属大类"
                @cancel="show = false"
                @confirm="confirmEvt"
                show-toolbar
                :columns="columns"/>
        </van-popup>
        
                        
    </layout>
</template>

<script>
    import { materialAddApi, materialListApi, materialSaveApi } from '../../apis/MaterialApi'

    export default {
        data() {
            return {
                isEdit: false,
                id: '',
                name: '',
                parentId: '',
                parentName: '',
                remark: '',
                nameRule: [
                    { required: true, message: '请填写用户名' },
                    { validator: (value, rule) => {
                        return value.length > 4
                    }, message: '材料名字的长度不能小于4个字符', trigger: 'onBlur'}
                ],
                show: false,
                columns: []
            }
        },
        created() {
            let data = this.$route.query
            this.isEdit = !!data.id
            if (this.isEdit) {
                this.id = data.id
                this.name = data.name
                this.parentId = data.parentId
                this.parentName = data.parentName
                this.remark = data.remark
            }
        },
        methods: {
            // 如果页面有校验规则，校验规则没有通过，不会调用此方法，否则会调用
            async onSubmit() {
                this.$toast.loading({
                    message: '保存中...',
                    forbidClick: true,
                    duration: 0
                })
                
                let obj = {
                    id: this.id,
                    name: this.name,
                    parentId: this.parentId,
                    parentName: this.parentName,
                    remark: this.remark
                }

                let result
                if (this.isEdit) {
                    result = await materialSaveApi(obj)
                } else {
                    result = await materialAddApi(obj)
                }

                this.$toast.clear()

                if (result.code === 200) {
                    this.$toast.success(`${this.isEdit ? '修改' : '新增'}成功`)
                } else {
                    this.$toast.fail(`${this.isEdit ? '修改' : '新增'}成功`)
                }
            },
            confirmEvt(obj) {
                this.parentId = obj.id || ''
                this.parentName = obj.name || ''
                this.show = false
            },
            async pickType() {
                let result = await materialListApi()

                this.columns = result.code === 200 ? result.data.rows.reduce((nl, it) => {
                    if (!it.parentId) {
                        it.text = it.name
                        nl.push(it)
                    }
                    return nl
                }, []) : []

                this.columns.unshift({text: '请选择'})
                this.show = true
            }
        }
    }
</script>

<style lang="less" scoped>
    @import "../../style/common.less";

    .edit-container {
        > .title {
            display: block;
            font-size: .7rem;
            line-height: 42px;
            color: @font-8c;
        }
        display: block;
        padding: 12px 12px 0;
        .btn {
            margin-top: 16px;
        }
    }
</style>